/* Color Variables */
:root {
    --dk-green: #5b643f;
    --green: #BBC893;
    --lt-green: #E0E6B3;
    --lt-tan: #FEFAE0;
    --tan: #FAEDCD;
    --dk-tan: #D4A373;
    --brown: #755E2E;
    --black: #1F140A;
    --link-blue: #92a8d1;
}

body {
    background-color: var(--lt-tan) !important;
    background-size: cover;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: var(--dk-green);
}

main {
    height: 100%;
    margin-top: 6%;
    margin-bottom: 6%;
}

hr {
    margin: 0;
}

.navbar {
    display: block !important;
    background-color: var(--green) !important;
    position: fixed !important;
    top: 0;
    width: 100%;
    z-index: 100;
}

.img {
    float: left;
    height: 250px;
    margin: 0 20px 0 0;
    border-radius: 6em;
}

.link {
    color: var(--link-blue);
}

.footer {
    margin-top: auto;
    background-color: var(--green) !important;
    position: fixed;
    bottom: 0;
    padding: 18px;
    font-size: 10px;
    color: white;
    text-align: center;
    width: 100%;
}

.active {
    font-weight: 600;
    color: var(--black);
}

.portfolio-img {
    margin-bottom: 20px;
    opacity: 100%;
}

.icon {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 30px;
}

h1 {
    color: var(--black);
}

h4 {
    margin-bottom: 0;
}

.pageTitle {
    padding-bottom: 10px;
    text-align: center;
}

div>hr {
    background-color: var(--dk-green);
}

/* BIO */
#about-me {
    /* background-color: var(--green); */
    padding-bottom: 10px;
    color: var(--black);
}

.skills {
    border: 1px solid var(--dk-green);
    margin: .25em;
    border-radius: 0.5em;
    position: relative;
    background: var(--lt-tan);
    box-shadow: 2px 2px 7px -2px var(--dk-green);
    font-size: 1.25em;
}

