.bar {
    padding: .5em;
    background-color: var(--dk-green);
    border-radius: .5em;
    color: var(--lt-tan);
    font-weight: bold;
    height: .25em;
    line-height: 0em;
}

.skillDetails {
    display: inline-block;
}

.skillLevel {
    float: right;
}

/* END BIO */

/* PORTFOLIO */

.card {
    box-shadow: 2px 2px 7px -2px var(--black);
    border: transparent !important;
    border-radius: .25em !important;
}

.card-img-top {
    height: 218px;
}

.card-body {
    background-color: var(--dk-green);
    color: var(--lt-tan);
    border-radius: 0 0 0.25em 0.25em;
}

div>.btn {
    border-radius: 2em;
    width: 100%;
    padding: .5em;
}

div>.btn-primary {
    background-color: white !important;
    border-color: white !important;
    box-shadow: 2px 2px 7px -2px var(--brown);
    color: var(--black);
}

div>.btn-secondary {
    background-color: var(--dk-green);
    border-color: var(--dk-green);
    box-shadow: 2px 2px 7px -2px var(--brown);
    color: var(--lt-tan);
    padding: .25em 1em;
    font-size: 1.25em;
}


/* JOBS */
#portfolio {
    display: grid;
}

div>.job-container {
    justify-self: center;
    display: grid;
    margin: 2em;
}

.job-header {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
}

.image-role-dates {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
}

.role-dates {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    gap: 1rem;
    height: 100%;
    width: 100%;
}

.role,
.dates {
    width: 100%;
    text-align: left;
    white-space: nowrap;
}

.company,
.location {
    text-align: right;
}

.company-location {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
}

.chip-container {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem
}

.chip {
    flex: 1;
    padding: 12px 24px;
    border-radius: 6px;
    white-space: nowrap;
    text-align: center;
    background-color: var(--dk-green);
    color: var(--lt-tan);
    font-weight: bold;
    border-radius: 1em;
    width: auto;
}

li {}


.project-description {
    text-align: left;
}

.project-image {
    width: auto;
}

/* END PORTFOLIO */

/* CONTACT ME */
form {
    align-items: center;
    display: flex;
    flex-direction: column;
    font-size: 1em
}

.field {
    padding: 1em;
}

.field input,
.field textarea {
    padding: 0.5em;
    border-radius: 0.5em;
}